body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.active{
  background-color: white;
  color: black;
  margin: 0px;
  padding: 0px;
}
.nav-link{
  color:white !important;
}
.color-black{
  color:black !important;
}
.pull-down{
  width: 100vw !important;
}
.color-white{
  color:white;
}
.icon-white{
  color:white;
}
#search{
  background-color: white;
  color:black;
}
#search:hover{
  background-color: white;
  color:black;
}
.footer-background{
  background-color:#333A40 !important;
}
.some-margin{
  margin:5px;
}
.dropdown-toggle::after{
    display:none !important;
}
a:hover{
  color: white;
}
#news{
    width: 100%;
    height: 516px;
    overflow: hidden;
    margin-top: 10px;
    margin-bottom: 10px;
    position: relative;
}
#news-small{
    width: 100%;
    height: 250px;
    overflow: hidden;
    margin-top: 10px;
    margin-bottom: 10px;
    position: relative;
}
.crop {
    position: absolute;
    left: -100%;
    right: -100%;
    top: -100%;
    bottom: -100%;
    margin: auto;
    height: auto;
    width: auto;
}
.title{
  position:absolute !important;
  background-color: #352a2a;
  color:white;
  font-size: 30px;
  width: 96%;
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 20px;
  bottom: 0px;
  padding-left: 8px;

}
.title-small{
  position:absolute !important;
  background-color: #352a2a;
  color:white;
  font-size: 20px;
  width: 96%;
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 20px;
  bottom: 0px;
  padding-left: 4px;

}

.dropdown-menu-center {
    right: auto;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    -o-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
}
.d-mx-none{
  display: inline !important;
}
.d-lgx-none{
  display:none !important;
  width: 100% !important;
}
.menu{
  background-color:#333A40 !important;
  z-index: 10;
}
.login{
  top: 10% !important;
}
.register{
  top: 5% !important;
}
.full-width{
  width: 100% !important;
}
@media only screen and (max-width: 992px) {
  .d-mx-none{
    display:none !important;
  }
  .d-lgx-none{
    display:inline !important;
  }
  #news{
      width: 100%;
      height: 250px;
      overflow: hidden;
      margin-top: 10px;
      margin-bottom: 10px;
      position: relative;
  }
  .title{
    position:absolute !important;
    background-color: #352a2a;
    color:white;
    font-size: 20px;
    width: 96%;
    margin-left: 2%;
    margin-right: 2%;
    margin-bottom: 20px;
    bottom: 0px;
    padding-left: 8px;

  }
}
